<template>
  <div id="translateForm">
    <form @submit="formSubmit">
        <input class="word" type="text" v-model="textToTranslate" placeholder="Enter a word...">
        <div class="select-wrap">
          <select v-model="language">
            <option value="ru">Russian</option>
            <option value="es">Spanish</option>
            <option value="fr">France</option>
            <option value="zh">Chinese</option>
            <option value="cs">Czech</option>
          </select>
        </div>
        <input class="button" type="submit" value="Translate!">
    </form>
  </div>
</template>

<script>
export default {
  name: 'translateForm',
  data(){
      return{
          textToTranslate: '',
          language: ''
      }
  },
  created(){
    this.language = 'ru';
  },
  methods:{
      formSubmit(e){
        this.$emit('formSubmit', this.textToTranslate, this.language);
        e.preventDefault();
      }
  }
}
</script>

<style>
form{
  border: 1px solid #f5f5f5;
}
.word{
  width: 100%;
  border: none;
  padding: 20px 0;
  text-align: center;
  text-transform: capitalize;
  
}

.select-wrap select {
  padding: 10px 8px;
  border: 1px solid #f5f5f5;
  width: 100%;
  border-radius: 0;
  box-shadow: none;
  background: transparent;
  background-image: none;
  -webkit-appearance: none;
  text-align: center;
}
.button{
  cursor: pointer;
  width: 100%;
  border: none;
  background: #42b983;
  color: #fff;
  font-weight: bold;
  padding: 10px 8px;
  font-size: 20px;
}

</style>


