<template>
  <div id="app">
    <div class="container">
      <div class="header">
        <h1>Vue Translator</h1>
      </div>
      <TranslateForm @formSubmit="translateText"></TranslateForm>
      <TranslateOutput v-text="translatedText"></TranslateOutput>
    </div>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm';
import TranslateOutput from './components/TranslateOutput';

export default {
  name: 'app',
  components:{
    TranslateForm,
    TranslateOutput
  },
  data(){
    return{
      translatedText: ''
    }
  },
  methods:{
    translateText: function(text, language){
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180605T085022Z.7f4b83e4a11f9775.95d02467a17b1aa0e46848a05967f9a1ff6af360&lang='+ language +'&text='+ text)
      .then((response) => {
        this.translatedText = response.body.text[0];
      });
    }
  }
}
</script>

<style>
#app{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.container{
  max-width: 450px;
  margin: 150px auto;
  text-align: center;
}
.header{
  background:#42b983;
  padding: 1px; 
}
.header h1{
  color: #fff;
  text-transform: uppercase;
  font-size: 35px;
}
</style>



